:root {
  --primary-color: #00f2ff;
}

* {
  box-sizing: border-box;
  user-select: none;
  margin: 0;
  padding: 0;
}

body, html {
  color: #fff;
  background-color: #020204;
  width: 100%;
  height: 100%;
  font-family: Share Tech Mono, monospace;
  overflow: hidden;
}

#bg-system {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

#bg-system #bg-blur-sync {
  filter: blur(40px) brightness(.4) saturate(1.8);
  background-position: center;
  background-size: cover;
  width: 150%;
  height: 150%;
  transition: background-image 2s cubic-bezier(.4, 0, .2, 1);
  animation: 60s linear infinite bgRotate;
  position: absolute;
  top: -25%;
  left: -25%;
}

#bg-system .bg-overlay {
  z-index: 1;
  background: radial-gradient(circle, #02020433 0%, #020204 90%) 0 0 / 100% 100%, linear-gradient(#00f2ff08 1px, #0000 1px) 0 0 / 50px 50px, linear-gradient(90deg, #00f2ff08 1px, #0000 1px) 0 0 / 50px 50px;
  width: 100%;
  height: 100%;
  position: absolute;
}

#bg-system .scanlines {
  z-index: 2;
  pointer-events: none;
  background: linear-gradient(#12101000 50%, #0003 50%) 0 0 / 100% 4px;
  width: 100%;
  height: 100%;
  position: absolute;
}

#bg-system #particles-js {
  z-index: 3;
  opacity: .6;
  width: 100%;
  height: 100%;
  position: absolute;
}

@keyframes bgRotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.ui-decoration {
  z-index: 100;
  color: var(--primary-color);
  letter-spacing: 2px;
  opacity: .5;
  text-transform: uppercase;
  padding: 20px;
  font-size: 12px;
  position: absolute;
}

.ui-decoration.ui-top-left {
  border-left: 2px solid var(--primary-color);
  top: 0;
  left: 0;
}

.ui-decoration.ui-bottom-right {
  border-right: 2px solid var(--primary-color);
  text-align: right;
  bottom: 0;
  right: 0;
}

#main-stage {
  perspective: 2500px;
  z-index: 10;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

#main-stage .view-container {
  aspect-ratio: 16 / 9;
  width: 75vw;
  max-width: 1200px;
  transform-style: preserve-3d;
  position: relative;
}

.image-viewport {
  background: #000;
  border: 1px solid #00f2ff26;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 80px #00f2ff26;
}

.image-viewport .main-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  transition: transform 1.2s cubic-bezier(.16, 1, .3, 1), opacity .6s;
}

.image-viewport .glitch-flash {
  animation: .3s linear glitch-anim;
}

@keyframes glitch-anim {
  0% {
    filter: hue-rotate() brightness();
    transform: scale(1.05);
  }

  20% {
    filter: hue-rotate(90deg) brightness(2);
    transform: translate(-5px, 5px);
  }

  40% {
    filter: hue-rotate(-90deg) contrast(2);
    transform: translate(5px, -5px);
  }

  100% {
    filter: none;
    transform: scale(1);
  }
}

@keyframes bgFloat {
  0% {
    transform: scale(1.1)translate(0)rotate(0);
  }

  100% {
    transform: scale(1.3)translate(-2%, -2%)rotate(3deg);
  }
}

.info-layer {
  pointer-events: none;
  z-index: 20;
  width: 450px;
  position: absolute;
  top: 50%;
  left: -80px;
  transform: translateZ(150px)translateY(-50%);
}

.info-layer .serial-num {
  color: #fff3;
  z-index: -1;
  letter-spacing: -10px;
  font-family: Orbitron, sans-serif;
  font-size: 8rem;
  font-weight: 700;
  line-height: .6;
}

.info-layer .title-box {
  backdrop-filter: blur(20px);
  border-left: 6px solid var(--primary-color);
  clip-path: polygon(0 0, 100% 0, 100% 85%, 90% 100%, 0 100%);
  background: #050508d9;
  padding: 2.5rem;
  box-shadow: 20px 20px 60px #000c;
}

.info-layer .title-text {
  color: var(--primary-color);
  text-shadow: 0 0 20px #00f2ff80;
  margin-bottom: 10px;
  font-family: Orbitron, sans-serif;
  font-size: 2rem;
}

.info-layer .desc-text {
  color: #999;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.6;
}

.nav-controls {
  gap: 20px;
  display: flex;
  position: absolute;
  bottom: -80px;
  right: 0;
  transform: translateZ(100px);
}

.nav-controls .nav-btn {
  cursor: pointer;
  width: 60px;
  height: 60px;
  color: var(--primary-color);
  background: #ffffff0d;
  border: 1px solid #00f2ff26;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.nav-controls .nav-btn:hover {
  background: var(--primary-color);
  color: #000;
  box-shadow: 0 0 30px var(--primary-color);
  transform: scale(1.1);
}

.nav-controls .nav-btn svg {
  fill: currentColor;
  width: 30px;
  height: 30px;
}

.indicators {
  gap: 15px;
  display: flex;
  position: absolute;
  bottom: 40px;
  right: 40px;
  transform: translateZ(120px);
}

.indicators .indicator-dot {
  cursor: pointer;
  background: #ffffff1a;
  width: 40px;
  height: 4px;
  transition: all .4s;
  transform: skewX(-30deg);
}

.indicators .indicator-dot.active {
  background: var(--primary-color);
  width: 70px;
  box-shadow: 0 0 20px var(--primary-color);
}

@media (width <= 1024px) {
  .main-stage .view-container {
    width: 95vw;
  }

  .info-layer {
    width: 300px;
    top: 80%;
    left: 10px;
  }

  .info-layer .serial-num {
    font-size: 5rem;
  }
}
